* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  background-image: url(../img/back.jpg);
  background-position: bottom center;
  background-size: contain;
  background-repeat: no-repeat; }

.page {
  font-family: "FuturaPT Book", Trebuchet, Arial, sans-serif; }
  .page__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    width: 100%;
    padding-top: 45px; }
  .page__content {
    display: flex;
    flex-direction: column;
    flex-grow: 2; }
  .page__footer-wrapper {
    display: flex;
    flex-grow: 1; }

.header__text {
  margin-bottom: 45px;
  text-align: center;
  font-size: 18px;
  color: #333; }

.logo__link {
  display: flex;
  flex-direction: row;
  justify-content: center; }
  .logo__link--left {
    animation: 1s ease bounceInLeft; }
  .logo__link--right {
    animation: 1s ease bounceInRight; }
  .logo__link:hover {
    transform: scale3d(1.05, 1.05, 1.05); }
  .logo__link img {
    display: block;
    max-width: 100%; }

.title {
  margin-top: 83px;
  margin-bottom: 21px;
  text-align: center;
  color: #003770;
  font-family: 'FuturaPT Medium', 'Trebuchet', Arial, sans-serif;
  font-size: 30px; }

.text {
  text-align: center;
  color: #333;
  font-family: "FuturaPT Book", Trebuchet, Arial, sans-serif;
  font-size: 18px; }

.offer {
  margin-top: 60px; }
  .offer__wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    min-height: 80px;
    margin-bottom: 20px;
    font-family: 'FuturaPT Medium', 'Trebuchet', Arial, sans-serif;
    font-size: 18px;
    color: #333;
    background: linear-gradient(45deg, #f2f2f2 0%, #fdfdfd 74%);
    box-shadow: 0px 7px 10px 0px rgba(0, 0, 0, 0.09);
    animation: zoomIn 1s ease; }
  .offer__text {
    display: block;
    position: relative;
    z-index: 5;
    text-align: center; }
  .offer__button {
    display: block;
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    margin-top: 34px;
    margin-bottom: 15px;
    padding: 12px 30px;
    border: 0;
    outline: 0;
    background-color: #ff6923;
    color: #fff;
    font-family: "FuturaPT Book", Trebuchet, Arial, sans-serif;
    font-size: 16px;
    cursor: pointer; }
    .offer__button:hover {
      border-radius: 3px;
      animation: pulse 0.5s ease; }
  .offer__mailto {
    text-align: center;
    font-size: 14px;
    color: #333; }
    .offer__mailto-link {
      font-family: 'FuturaPT Demi', 'Trebuchet', Arial, sans-serif;
      color: #333;
      text-decoration: none; }
      .offer__mailto-link:hover {
        color: #ff6923; }

.comment {
  margin: 112px auto 80px auto;
  max-width: 820px;
  text-align: center;
  color: #333;
  font-size: 14px;
  line-height: 18px; }

@media (max-width: 992px) {
  .page__inner {
    padding-top: 30px; }
  .logo__link {
    width: 250px;
    margin-left: auto;
    margin-right: auto; }
  .title {
    margin-top: 50px;
    margin-bottom: 20px; }
  .comment {
    margin-bottom: 60px; } }

@media (max-width: 480px) {
  .page__inner {
    padding-top: 30px; }
  .header__text {
    margin-bottom: 20px;
    font-size: 14px; }
  .logo__link {
    width: 250px;
    margin-left: auto;
    margin-right: auto; }
  .title {
    margin-top: 33px;
    margin-bottom: 11px;
    font-size: 24px; }
  .text {
    font-size: 14px; }
  .offer {
    margin-top: 35px; }
    .offer__wrap {
      min-height: 70px;
      margin-bottom: 21px;
      font-size: 16px; }
    .offer__button {
      margin-top: 12px;
      padding: 7px 22px; }
    .offer__mailto {
      font-size: 12px; }
  .comment {
    margin-top: 64px;
    margin-bottom: 60px;
    font-size: 12px; } }
